header .nav-primary {
  @apply sm:flex sm:flex-row items-center;
}
header .nav-primary ul {
  @apply sm:flex sm:flex-row items-center;
}
header .nav-primary ul li {
  @apply px-2;
}

footer .nav-footer {
  @apply sm:flex sm:flex-row items-center;
}
footer .nav-footer ul {
  @apply sm:flex sm:flex-row items-center;
}
footer .nav-footer ul li {
  @apply px-2;
